import { Card, Col, Row } from "antd";
import React from "react";
import type { HeadFC, PageProps } from "gatsby";
import PageLayout from "../../components/layouts/PageLayout";
import SEO from "../../components/Seo";
import Profile from "../../components/Profile";
import farmer from "../../images/farmers/gurmeet-singh.jpg";

const Page: React.FC<PageProps> = () => {
  return (
    <PageLayout>
      <h1>Influenced by reformers - Gurmeet Bhawalpur</h1>

      <Row gutter={18}>
        <Col xs={24} md={24} lg={24}>
          <Profile designation="" image={farmer} />
        </Col>
      </Row>

      <Card title="" bordered={false}>
        <p>
          45-year-old Gurmeet is devoted to social transformation through
          natural farming.
        </p>
        <p>
          “Get down at Lanjan bus stop and ask anyone about the farmer who rears
          desi cows and does organic farming,” Gurmeet Singh Bahawalpur said
          over the phone. His farm is just around 100 metres from the bus stop,
          marked by a big shed sheltering 18 cows and calves, all indigenous
          breeds.
        </p>
        <p>
          The 10-acre farm extends with a vegetable patch in front, followed by
          an orchard of guava, pomegranate, amla and naseberry. Land for grains
          is neatly portioned to the right. A pond on 2.5 bigha collects rain
          water and supply from the canal, to be used later. No wonder this farm
          is famous. Two farmers arrive as soon as I sit down with Gurmeet to
          have tea. They want his advice on which indigenous breeds of cows to
          buy.
        </p>
        <p>
          After discussing the colour, hair, hump, behaviour of cows, they talk
          about fruits at his orchard. I had already gone ahead and devoured the
          wonderful guavas. Deeply influenced by Guru Nanak’s teachings of
          compassion, Bhagat Singh’s socialism and Osho’s discourses on
          detachment, Gurmeet was always looking for a way out of chemical-laden
          farming. This desire was known to most of his friends and neighbours,
          one of whom happened to attend a meeting of the Kheti Virasat Mission,
          and informed Gurmeet about this opportunity to learn. It was a turning
          point he was waiting for. After collecting much information about
          organic farming, Gurmeet started with 5 acres and within two years had
          whole of his 10-acre farm chemical free. The wheat production dropped
          to 4 quintals per acre in the first year but due to his efforts every
          acre of the land now yields 9-11 quintal.
        </p>
        <p>
          The farm which, being highly acidic, was not suitable to grow
          vegetables is now sending the greens to families in Chandigarh,
          Patiala and Rajpura, testifying that 45-year-old Gurmeet has come a
          long way in last 10 years. “I am consistently reducing area under
          paddy, replacing it with vegetables. Paddy is now confined to 2
          acres,” he says.
        </p>
        <p>
          Under the guidance of expert farmers like Prakash Raghuvanshi and
          Suresh Desai, Gurmeet adopted several traditional seeds and innovative
          techniques to maximise benefits from organic farming. He has also
          managed to deal with infestation of weeds, one of the side effects of
          using natural manure like cow dung. I have realised that rotating the
          crops can easily help deal with this issue. So after harvesting wheat,
          I grow green fodder on the same plot and replace paddy with corn,” he
          says.
        </p>
        <p>
          With untimely demise of his younger brother, Gurmeet also got the
          responsibility of leasing out another 10 acre farm belonging to his
          brother’s family. Fortunately, a lawyer from Patiala, also interested
          in organic farming, stepped forward, and now Gurmeet is also guiding
          him on how to pursue the nature’s ways. The marketing of his produce
          has gone places. Several families pick up the wheat while Punjab Agro
          Industries Corporation Ltd, a government organisation, procured
          Basmati variety of rice last year at a premium of Rs 500 per quintal
          over and above the prevailing market rate. The vegetables go to nearby
          cities while a group of youth from Chandigarh also procure milk of the
          indigenous cows, which is known to be beneficial for health.
        </p>
        <p>
          But the motive for Gurmeet to keep these many cows is not dairy, but
          use of their dung to improve soil fertility. The pond, built with
          subsidy of Rs 8 lakh from the State Horticulture Department, has helps
          save groundwater and money earlier spent on running the motors. “The
          use of borewell has gone down by one fourth. This was a big issue as
          the quality of groundwater, which is available at 250 feet in this
          area, is not good and hence impacts the crops. Moreover, I can easily
          drain out water from the wheat fields to the pond instead of pleading
          neighbouring farmers,” Gurmeet informs.
        </p>
        <p>
          The man, however, desires greater family support. “My wife and
          children are quite supportive of what I do, but they don’t get
          involved. Organic farming is a family effort because it demands lot of
          sustained effort which can’t be expected from hired labourers. But the
          way things are going in our modern society, roles have been fixed.
          Women are now confined mostly to homes while children are supposed to
          be restricted to studies. Means of entertainment like television and
          mobile phones take up any idle time. Organic farming demands simpler
          lifestyle which is a rarity nowadays,” Gurmeet regrets. But he has a
          solution for this as well. “Farmers pursuing natural ways can join
          hands with each other and pursue cooperative farming. That’s the only
          way things can improve,” he signs off.
        </p>
      </Card>
    </PageLayout>
  );
};

export default Page;

export const Head: HeadFC = () => (
  <SEO title="Influenced by reformers - Gurmeet Bhawalpur" />
);
